/* Write CSS here */
* {
  box-sizing: border-box;
  margin: 0px;
}
.cont {
  background-image: url('https://assets.ccbp.in/frontend/react-js/stopwatch-sm-bg.png');
  height: 100vh;
  background-size: cover;
}
.content {
  padding-left: 120px;
  padding-top: 50px;
}
@media (min-width: 768px) {
  .cont {
    background-image: url('https://assets.ccbp.in/frontend/react-js/stopwatch-lg-bg.png');
    height: 100vh;
    background-size: cover;
  }
  .content {
    padding-left: 220px;
    padding-top: 50px;
  }
}

.timer {
  padding-left: 5px;
  padding-top: 5px;
}
.flex-row {
  display: flex;
  margin: auto;
}
.stop-watch-head {
  padding-left: 15px;
  padding-bottom: 10px;
}
.icon {
  width: 25px;
  height: 30px;
}
.card {
  box-shadow: 0px 4px 16px 0px;
  width: 200px;
  height: 200px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  text-align: center;
}
.btn-cont {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 10px;
}
.start {
  background-color: green;
  color: white;
  border-radius: 10px;
  border: none;
  padding: 10px;
}
.stop {
  background-color: red;
  color: white;
  border-radius: 10px;
  border: none;
  padding: 10px;
}
.reset {
  background-color: orange;
  color: white;
  border-radius: 10px;
  border: none;
  padding: 10px;
}
